:root {
  --font-body: Arial, sans-serif;
}

body {
  font-family: "Overpass", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 16px;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.open {
  visibility: visible !important;
}

.hide {
  visibility: hidden !important;
}

@media (min-width: 64em) {
  .hide-for-desktop {
    visibility: hidden;
  }
}

h1 {
  color: #1f3f5b;
}

p {
  color: #4b5862;
}

p,
h1,
h2,
h3 {
  margin: 0;
  font-size: 16px;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.4rem;
  margin: 0 auto;
  max-width: 90rem;
}

.footer {
  background: #25252d;
  border-top-right-radius: 100px;
  padding: 77px 0 0 0;
  display: flex;
  align-items: center;
  align-content: center;
  flex-direction: column;
}
.footer > img {
  width: 6.5rem;
  height: auto;
}
.footer > nav {
  padding-top: 80px;
}
.footer > nav .row {
  display: flex;
  align-items: center;
  align-content: center;
  flex-direction: column;
  gap: 10px;
  padding-bottom: 45px;
}
.footer > nav .row > h3 {
  color: white;
  font-size: 20px;
  padding-bottom: 18px;
}
.footer > nav .row > a {
  color: #c8c8cb;
  font-size: 19px;
}

.header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 3.375rem 0 0 0;
}
.header__nav__logo {
  width: 5.1875rem;
  height: 2.1rem;
}

.menu-hamburger {
  visibility: hidden;
  padding-top: 3.5rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.menu-hamburger .close {
  width: 87%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.menu-hamburger .menu-wrapper {
  background: white;
  width: 87%;
  height: auto;
  border-radius: 5px;
  margin-top: 40px;
  padding: 13px 25px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1), 0px 20px 30px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.menu-hamburger .menu-wrapper .items {
  width: 100%;
}
.menu-hamburger .menu-wrapper .items:hover .items__sub-menu {
  display: flex;
}
.menu-hamburger .menu-wrapper .items:hover .row {
  transform: rotate(-180deg);
}
.menu-hamburger .menu-wrapper .items__header {
  padding: 25px 0 7px 0;
  gap: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu-hamburger .menu-wrapper .items__header > h3 {
  color: #1f3f5b;
  font-size: 18px;
  cursor: pointer;
}
.menu-hamburger .menu-wrapper .items__header .row {
  transition: all 0.5s ease-in-out;
}
.menu-hamburger .menu-wrapper .items__sub-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #efeff1;
  border-radius: 5px;
  width: 100%;
  margin-top: 15px;
  display: none;
}
.menu-hamburger .menu-wrapper .items__sub-menu a {
  display: block;
  padding: 1rem;
  color: #4b5862;
}
.menu-hamburger .menu-wrapper .items__sub-menu a:hover {
  color: #1f3f5b;
}
.menu-hamburger .menu-wrapper hr {
  background-color: #efeff1;
  width: 100%;
  margin-top: 16px;
}
.menu-hamburger .menu-wrapper .btn-wrapper {
  margin: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
}
.menu-hamburger .menu-wrapper .btn-wrapper .login {
  color: #4b5862;
  padding: 14px 0px;
  border-radius: 5rem;
  width: 140px;
  font-weight: 700;
  text-align: center;
}
.menu-hamburger .menu-wrapper .btn-wrapper .login:hover {
  color: white;
  background: #25252d;
}
.menu-hamburger .menu-wrapper .btn-wrapper .singup {
  background: linear-gradient(135deg, #ff7a85, #ff525d);
  color: white;
  border: 1px solid #ff525d;
  padding: 14px 0px;
  border-radius: 5rem;
  width: 140px;
  font-weight: 700;
  text-align: center;
}
.menu-hamburger .menu-wrapper .btn-wrapper .singup:hover {
  border: 1px solid #ff525d;
  color: #ff525d;
  background: white;
}

.hero {
  padding-top: 87.59px;
  height: 600px;
  background-image: url(/app/images/bg-pattern-intro.svg), linear-gradient(0deg, #ff3d54, #ff8f70);
  background-size: 350%, 100%;
  background-position: 35% 34%, 0% 0%;
  background-repeat: no-repeat, no-repeat;
  border-bottom-left-radius: 100px;
}
.hero__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 112px 0;
  text-align: center;
}
.hero__wrapper > h1 {
  padding-bottom: 18px;
  font-weight: 500;
  font-size: 34px;
  line-height: 2.9rem;
  color: white;
}
.hero__wrapper > p {
  font-size: 18px;
  color: white;
}
.hero__wrapper .buttons {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-around;
  margin-top: 49px;
  width: 95%;
}
.hero__wrapper .buttons > a {
  padding: 14px 0px;
  border-radius: 5rem;
  width: 140px;
  font-weight: 700;
  font-family: Ubuntu, sans-serif;
  color: white;
  border: 1px solid white;
}
.hero__wrapper .buttons > a.start-button {
  background-color: white;
  color: #ff525d;
}
.hero__wrapper .buttons > a.start-button:hover {
  background-color: #ff7a85;
  color: white;
}
.hero__wrapper .buttons > a.learn-button {
  color: white;
}
.hero__wrapper .buttons > a.learn-button:hover {
  background-color: white;
  color: white;
  color: #ff525d;
}

.section {
  display: flex;
  align-items: center;
  justify-items: center;
  flex-direction: column;
}
.section > h1 {
  padding-top: 107px;
  color: #1f3f5b;
  font-size: 26.5px;
}
.section__wrapper {
  overflow: hidden;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.section__wrapper > img {
  transform: scale(1.25);
  width: 100%;
  height: auto;
  padding: 77px 0;
}
.section__wrapper__info {
  text-align: center;
}
.section__wrapper__info .info > h1 {
  padding-top: 9px;
  font-size: 27px;
}
.section__wrapper__info .info > p {
  padding-bottom: 35px;
  padding-top: 16px;
  font-family: "Ubuntu", sans-serif;
  font-size: 17px;
  line-height: 1.7;
}

.middle {
  position: relative;
  height: 15rem;
}
.middle img {
  padding: 54px 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(1.01);
  width: 372px;
}

.middle-section {
  background: #3f4164;
  border-top-right-radius: 100px;
  border-bottom-left-radius: 100px;
  padding-top: 215px;
}
.middle-section .section__wrapper {
  padding-bottom: 80px;
}
.middle-section .section__wrapper .info h1 {
  color: white;
  padding-bottom: 3px;
  font-size: 40px;
  line-height: 1.2;
}
.middle-section .section__wrapper .info p {
  color: #c8c8cb;
}

.last-section {
  padding-bottom: 64px;
}
.last-section img {
  transform: scale(1.5);
  padding-top: 112px;
}